
/* HEADER */
.header {
    display: flex;
    flex-direction: column;
    overflow: hidden;
    align-items: center;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 999;
    width: 100%;
    border-bottom: 2px solid rgb(236, 240, 241);
}

.header--text {
    margin-right: 10rem;
}

.header--container {
    width: 75%;
    height: 100%;
    bottom:1rem;
}

.header--nav {
    width: 100%;
    height:100%;
}

.header--item {
    width: 100%;
    height:100%;
}

.header--text {
    font-size: 3rem;
    color: rgb(236, 240, 241);
    margin-left: 2rem;
    float: left;
}



.section--social {
    float: right;
    margin-right: 2rem;
