}

.social--text {
    margin-right: 2.5rem !important;
}

.social:hover {
    color: rgb(236, 240, 241);
    text-decoration: none;
}

.social:visited {
    color: rgb(236, 240, 241);;
}

.socialWrapper:last-child .social {
